@media (min-width: $tablet-width) and (max-width: $end-tablet) {
  .weight__heading {
    font-size: 36px;
    line-height: 37px;
    margin-bottom: 30px;
    padding-left: 0;
  }
  .weight__block {
    min-height: 237px;
    padding: 80px 300px 55px 55px;
  }
  .weight__text {
    font-size: 16px;
    line-height: 24px;
    border: none;
    padding-bottom: 40px;
  }
  .weight__link {
    max-width: 156px;
    font-size: 20px;
    line-height: 20px;
  }
  .weight__heading::before {
    width: 200px;
    height: 200px;
    top: 90px;
    left: auto; 
    right: 62px;
  }
  .weight__heading--loss::after {
    width: 140px;
    height: 194px;
    top: 78px;
    left: auto;     //переопределить left на мобильном
    right: 92px;
  }
  .weight__heading--gain::after {
    width: 268px;
    height: 112px;
    top: 135px;
    left: auto; 
    right: 27px;
  }
}

@media (min-width: $desktop-width) {
  .weight {
      //flex-wrap: wrap;
      flex-direction: row;
      align-items: stretch;
  }

  .weight__block { 
    //display: inline-block; 
    max-width: 530px;
    margin-right: 80px;
    margin-bottom: 80px;
    padding-top: 80px;
  }

  .weight__block:nth-child(2n) {
    margin-right: 0;
  }

  .weight__heading {
    font-size: 36px;
    line-height: 37px;
    padding-bottom: 80px;
    padding-left: 216px;
  }

  .weight__heading::before {
    width: 100px;
    height: 100px;
    top: 47px;
    left: 53px;
  }

  .weight__heading--loss::after {
    width: 70px;
    height: 97px;
    top: 41px;
    left: 66px;
  }

  .weight__heading--gain::after {
    width: 134px;
    height: 56px;
    top: 68px;
    left: 34px;
  }

  .weight__text {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 35px;
    margin: 0;
    border: none;
  }

  .weight__link {
    max-width: 157px;
    font-size: 20px;
    line-height: 20px;
  }
}
